.reportsHeader {
    background: url('../../assets/images/reports.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50% 20%;
    width: 100%;
    text-align: center;
    padding: 20px;
}

.reportsHeader h1{
    width: 50%;
    margin: 0 auto;
    background-color: rgba(0,0,0,0.5);
    text-transform: uppercase;
    border: 1px solid white;
    font-family: Oswald, sans-serif;
    font-size: 2rem;
    text-align: center;
    padding: 10px;
    color: white;
    clear: both;
}

