@import '../../../../../styles/colors.scss';

.medal-container {
    background: $default-ligther-gray;
    border-radius: 20px;
    height: 350px;
    margin: 10px;
    padding: 25px;
    width: 320px;

    .title {
        color: $apex-blue;
        font-size: 22px;
        font-weight: 700;
        margin: 0;
        text-align: center;

        span {
            color: $apex-orange;
            font-size: 26px;
            padding-right: 5px;
        }
    }

    .medal-image {
        margin-top: 10px;
    }
}
