.my_activity {
  margin: 10px 0px;
  table-layout: fixed;
}

.my_activity thead {
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.15);
}

.my_activity thead tr th, .my_activity tbody tr td {
  padding: 10px;
  word-wrap: break-word;
  text-align: center;
}

.my_activity tbody tr:nth-child(even) {
  background: #FFF;
}

.my_activity tbody tr:nth-child(odd) {
  background: rgba(20, 108, 181, 0.1);
}

.my_activity thead tr th.date, .my_activity tbody tr td.date {
  width: 180px;
}

.my_activity thead tr th.name, .my_activity tbody tr td.name {
  min-width: 200px;
}

.my_activity thead tr th.value, .my_activity tbody tr td.value {
  width: 80px;
}

.my_activity thead tr th.reason, .my_activity tbody tr td.reason {
  min-width: 200px;
  max-width: 300px;
}

.my_activity thead tr th.diploma, .my_activity tbody tr td.diploma {
  width: 80px;
}

.my_activity tbody tr td.diploma a i {
  color: #039be5;
}

.no_awards {
  padding: 30px;
  text-align: center;
}

.no_awards h2 {
  font-size: 18px;
}

.no_awards h2 span {
  color: #f88f3d;
  font-weight: bold;
}

.no_awards img {
  width: 10%;
  height: 10%;
}

img.core_value {
  width: 40px;
  height: 40px;
}

.date {
  width: 50px;
}