@import '../../../../styles/colors.scss';

.kudos-badges-container {
    background: $default-ligther-gray;
    border-radius: 20px;
    margin-top: 10px;
    min-width: 260px;
    padding: 5px 15px;
    text-align: center;

    .title {
        color: $apex-blue;
        font-size: 16px;
        font-weight: 700;
        margin: 0;
        text-align: center;
    }

    .kudos-badge {
        box-shadow: none;
        padding: 10px;
        transition: 0.5s;
        width: 90px;

        &:not(.active-badge) {
            filter: grayscale(100%);
            opacity: 0.5;
        }
    } 

    .tooltip-wrapper {
        float: left;
        transform: translateZ(0);
        z-index: 10;

        &:hover .tooltip {
            opacity: 1;
            pointer-events: auto;
            transform: translateY(0px);
            z-index: 12;
        }

        .tooltip {
            background: $tooltip-background;
            border: 2px solid $apex-orange;
            border-radius: 20px;
            bottom: 100%;
            box-shadow: 2px 2px 6px $tooltip-shadow;
            color: $default-white;
            display: block;
            font-size: 14px;
            font-weight: 300;
            left: -55px;
            margin-bottom: 15px;
            opacity: 0;
            padding: 15px;
            pointer-events: none;
            position: absolute;
            transform: translateY(10px);
            transition: all .25s ease-out;
            width: 200px; 
            z-index: 11;

            &:before {
                bottom: -20px;
                content: " ";
                display: block;
                height: 20px;
                left: 0;
                position: absolute;
                width: 100%;
            }
            
            &:after {
                border-left: solid transparent 10px;
                border-right: solid transparent 10px;
                border-top: solid $apex-orange 10px;
                bottom: -10px;
                content: " ";
                height: 0;
                left: 50%;
                margin-left: -10px;
                position: absolute;
                width: 0;
            }

            span {
                color: $apex-orange;
                font-size: 16px;
                font-weight: 700;
                text-transform: uppercase;
                margin-right: 5px;
            }
        }
    }
}
