@import '../../../../styles/colors.scss';

.buttons-container {
    align-items: center;
    display: inline-flex;
    justify-content: center;
    margin-top: 25px;

    button {
        background-color: $default-gray;
        color: $default-white;
        font-size: 16px;
        font-weight: 700;
        height: 60px;
        margin: 10px;
        opacity: 0.8;
        padding: 10px 15px;
    }

    .submit-and-add-more-button {
        background-color: $apex-orange;
    }

    .submit-button {
        background-color: $apex-blue;
    }

    .submit-and-add-more-button:hover,
    .submit-button:hover {
        opacity: 1;
    }
}
