@import '../../../styles/colors.scss';

$navbar-height: 65px;
$line-height-icons: 37px;
$logo-container-width: 150px;
$logo-size: 25px;


.navigation-bar {
    background-color: $default-white;
    border-bottom: 1px solid $default-ligther-gray;
    box-shadow: none;
    height: $navbar-height;

    .brand-logo {
        height: $navbar-height;
        width: $logo-container-width;
        
        a {
            align-items: center;
            display: flex;
            height: $navbar-height;
            justify-content: center;
            width: $logo-container-width;

            .logo {
                height: $logo-size;  
            }
        }
        
    }

    ul div {
        display: inline; 
    }

    li {
        height: $navbar-height;

        a, p {
            color: $default-dark-gray;
            font-size: 14px;
            height: $navbar-height;
            text-transform: uppercase;

            i {
                font-size: 22px;
            }

            &:hover {
                background-color: $default-ligther-gray;
                border-bottom: 2px solid $apex-blue;
            }
        }

        &.sign-out p {
            color: $apex-orange;

            &:hover {
                background-color: $apex-orange;
                border-color: $apex-orange;
                color: $default-white;
            }
        }
    }

    a.button-collapse {
        height: $navbar-height;

        i {
            color: $default-dark-gray;
            font-size: 30px;
            height: $navbar-height;
            line-height: $navbar-height;
        }
    }

    .dropdown-content {
        min-width: 180px;
        overflow: hidden;
        text-align: center;

        li a,
        li p {
            line-height: $line-height-icons;
            margin: 0;
            padding: 14px 16px;

            i {
                line-height: $line-height-icons;
            }
        }
    }

    .side-nav {
        li a,
        li a i {
            color: $default-dark-gray;
            line-height: $navbar-height;
        }

        .dropdown-content li a,
        .dropdown-content li a i {
            line-height: $line-height-icons;
            margin: 0;
        }
    }
}
