
@import '../../../../styles/colors.scss';

.values-container {
    margin-bottom: 30px;

    .title {
        color: #9e9e9e;
        font-size: 1.2rem;
        text-align: center;
    }

    .apex-blue {
        background-color: $apex-blue;
    }

    .apex-dark-blue {
        background-color: $apex-dark-blue;
    }

    .apex-orange{
        background-color: $apex-orange;
    }

    .apex-teal {
        background-color: $apex-teal;
    }

    .default-color {
        background-color: $default-gray;
    }

    .value {
        border-radius: 100%;
        box-shadow: none;
        color: #FFF;
        cursor: pointer;
        font-size: 14px;
        font-weight: 700;
        height: 160px;
        margin: 10px;
        padding: 10px;
        transition: 0.5s;
        width: 160px;

        &:hover {
            box-shadow: 0px 0px 5px 5px $active-shadow;
            opacity: 0.8;
        }
    }

    .active {
        box-shadow: 0px 0px 5px 5px $active-shadow;
        pointer-events: none;
    }
}
