@import '../../../styles/colors.scss';

.home-page {
    .home-header {
    background: url('../../../assets/images/banners/landing.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 0;
    padding: 25px;
    text-align: center;
    width: 100%;

        h1 {
            background-color: $banner-title;
            border: 1px solid $default-white;
            color: $default-white;
            font-size: 22px;
            margin: 0 auto;
            min-width: 180px;
            padding: 10px;
            text-align: center;
            text-transform: uppercase;            
            width: 60%;
        }

        button {
            background-color: $apex-orange;
            border: 2px solid $apex-orange;
            color: $default-white;
            font-size: 16px;
            font-weight: 700;
            height: 40px;
            line-height: 10px;
            margin-top: 15px;
            min-width: 175px;
            padding: 5px 10px;
            transition: 0.5s ease;
            z-index: 0;

            &:hover {
                background-color: $apex-blue;
                border-color: $default-white;
                color: $default-white;
                transition: 0.5s ease;
            }
        }
    }

    .home-container {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 20px;
        width: 100%;
    }
}
