@import '../../../styles/colors.scss';

.nomination-form {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .nomination-header {
        background: url('../../../assets/images/banners/new-nomination.jpg');
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 25px;
        text-align: center;
        width: 100%;

        h1 {
            background-color: $banner-title;
            border: 1px solid $default-white;
            color: $default-white;
            font-size: 22px;
            margin: 0 auto;
            min-width: 180px;
            padding: 10px;
            text-align: center;
            text-transform: uppercase;            
            width: 60%;
        }
    }

    &-container {
        margin: 30px;
        max-width: 900px;

        .fields-container {
            display: flex;
            flex-direction: column;
            width: 100%;

            .input-field .prefix ~ input,
            .input-field .prefix ~ label {
                margin-left: 0;
            }

            input[type=text]:focus:not([readonly]),
            textarea.materialize-textarea:focus:not([readonly]):not(.invalid) {
                border-bottom: 1px solid $apex-blue;
                color: $apex-blue;
                box-shadow: 0 1px 0 0 $apex-blue;
            }

            input[type=text]:focus:not([readonly]) + label,
            textarea.materialize-textarea:focus:not([readonly]) + label{
                color: $apex-blue;                
            }

            input[type=text] + label:after {
                bottom: 0px;
                font-size: 12px;
                margin-top: 5px;
            }

            textarea.materialize-textarea + label:after {
                bottom: 0px;
                font-size: 12px;
                margin-top: 43px;
            }
            
            .input-field {
                margin-bottom: 20px;
                margin-top: 20px;
            }

            .input-field .prefix {
                background-color: $default-white;
                font-size: 16px;
                margin-right: 10px !important;
                margin-top: 12px !important;
                padding: 0;
                padding-right: 0.75rem;
                right: 0;
                width: auto;
                z-index: 2;
            }

            .input-field .prefix.active {
                color: $apex-blue;
            }

            .input-field label {
                left: 0;
                width: 100%
            }

            .email-field {
                min-width: 350px;
                width: 50%
            }
        }
    }
}

//Toasts

.toast {
    top: 0;
}
  
.info-toast {
    background-color: $apex-orange;
    border: 2px solid $default-gray;
    border-radius: 10px;
    color: $default-black;
    font-size: 14px;
    text-transform: uppercase;
    padding: 15px;

    i {
        color: $apex-blue;
        margin-left: 5px;
    }
}
  
.success-toast {
    background-color: $apex-blue;
    border: 2px solid $default-gray;
    border-radius: 10px;
    color: $default-white;
    font-size: 14px;
    text-transform: uppercase;
    padding: 15px;

    i,
    span {
        color: $apex-orange;
        margin-left: 5px;
    }
  }
