@import '../../../../../styles/colors.scss';

.medals-container {
    .medal-tooltip-wrapper {
        float: left;
        transform: translateZ(0);

        &:hover .tooltip {
            opacity: 1;
            pointer-events: auto;
            transform: translateY(0px);
            z-index: 12;
        }

        .tooltip {
            background: $tooltip-background;
            border: 2px solid $apex-orange;
            border-radius: 20px;
            bottom: 100%;
            box-shadow: 2px 2px 6px $tooltip-shadow;
            color: $default-white;
            display: block;
            font-size: 14px;
            font-weight: 300;
            left: 40px;
            margin-bottom: 15px;
            opacity: 0;
            padding: 15px;
            pointer-events: none;
            position: absolute;
            transform: translateY(10px);
            transition: all .25s ease-out;
            width: 250px; 
            z-index: 11;

            &:before {
                bottom: -20px;
                content: " ";
                display: block;
                height: 20px;
                left: 0;
                position: absolute;
                width: 100%;
            }
            
            &:after {
                border-left: solid transparent 10px;
                border-right: solid transparent 10px;
                border-top: solid $apex-orange 10px;
                bottom: -10px;
                content: " ";
                height: 0;
                left: 50%;
                margin-left: -10px;
                position: absolute;
                width: 0;
            }

            p {
                margin: 0;
                text-align: center;
            }

            .highest-level,
            .text {
                color: $default-white;
                font-size: 14px;
            }

            .current-level,
            .highest-level span,
            .next-level {
                color: $apex-orange;
                font-size: 18px;
                text-transform: uppercase;
            }

            .count {
                color: $apex-teal;
                font-size: 26px;
            }
        }
    }
}