#awardModal {
  max-height: 80% !important;
}

#awardModal h4 {
  margin: 0;
  background-color: #168bcd;
  text-transform: uppercase;
  font-size: 2rem;
  text-align: center;
  padding: 10px;
  color: #FFF;
  clear: both;
}

#awardModal .col {
  padding: 0;
}

#awardModal ul.tabs {
  box-shadow: none;
  -webkit-box-shadow: none;
  color: #168bcd;
  background-color: #FFF;
}

#awardModal .tab-container {
  background-color: #FFF;
}

.awards-tabs>li {
  margin-left: 10px !important;
}

.awards-tabs>li>a {
  color: #9e9e9e !important;
  font-weight: bold;
  font-size: 16px !important;
}

.awards-tabs>li>a.active {
  color: #37b3a2 !important;
}

.awards-tabs .indicator {
  background-color: #37b3a2 !important;
}

.tab-container {
  overflow-x: hidden;
  overflow-y: auto;
  margin-bottom: 10px;
}

@media only screen and (min-width: 993px) {
  .tab-container {
    max-height: 480px;
  }
}

@media only screen and (max-width: 992px) {
  .tab-container {
    max-height: 480px;
  }
}

@media only screen and (max-width: 600px) {
  .tab-container {
    max-height: 200px;
  }
}

@media only screen and (min-width: 992px) {
  .tab-container {}
}