/*Filters*/

.filters, .reportTable {
  height: 100%;
  margin: 10px;
}

/*.filters i {
    float:right;
}*/

.filters input[type="radio"] .label {
  color: rgba(0, 0, 0, 0.87);
}

[type="radio"]:checked+label:after, [type="radio"].with-gap:checked+label:after {
  background-color: #72C168;
  border: 2px solid #72C168;
  ;
}

.filters .selector {
  max-height: 150px;
}

.filters h1, .reportTable h1 {
  margin-left: -0.85rem;
  margin-right: -0.85rem;
  margin-top: 0;
  margin-bottom: .75rem;
  background-color: #146CB5;
  font-family: Oswald, sans-serif;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  padding: 10px 20px;
  color: white;
}

.filters h2, .filters .input-field label {
  font-size: 1rem !important;
  text-transform: uppercase;
  text-align: left;
  color: #9e9e9e;
  margin: .75rem 0;
  cursor: text;
  transition: .2s ease-out;
}

.filters .rangeValues .rangeLabel {
  width: 50px;
  text-align: center;
}

.filters .rangeValues input {
  text-align: center;
}

.filters .rangeValues h6 {
  background-color: #FFF;
  color: #146CB5;
  font-size: .75rem;
  border: 1px solid #878787;
  padding: 5px 10px;
  float: left;
  margin-top: 2px;
}

.selectFilter .col {
  padding-left: 0 !important;
}

.selectFilter label {
  margin: 0 !important;
  margin-left: -12px !important;
}

/* Date Picker Styles */

.datepicker::placeholder {
  color: black;
}

.picker__weekday-display {
  background-color: #FFF;
  color: #72C168;
}

.picker__date-display {
  background-color: #146CB5;
}

.picker__footer button, .picker__day.picker__day--today {
  color: #72C168 !important;
}

.picker__day--selected, .picker__day--selected:hover, .picker--focused .picker__day--selected {
  border-radius: 15px 0px;
  background-color: #72C168;
  color: #FFF !important;
}

.filtersButtons {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.filtersButtons button {
  background-color: #08C1AF;
  color: #FFF;
  border: 2px solid transparent;
  font-size: 16px;
  font-weight: bold;
  height: 40px;
  padding: 0 10px;
  margin: 10px;
}

.filtersButtons button.apply {
  background-color: #72C168;
  color: #FFF;
}

.filtersButtons button.clear {
  border: 2px solid #146CB5;
  background-color: #FFF;
  color: #146CB5;
}

.filtersButtons button.apply:hover {
  background-color: #57934f;
}

.filtersButtons button.clear:hover {
  background-color: #146CB5;
  color: #FFF;
}